@import '../../vars.scss';

.navList {
  margin-top: 45px;
  display: flex;
  margin-bottom: 50px;
}

.navItem {
  padding: 0 5px;
  font-weight: 300;
  font-size: 14px;
  line-height: 120%;
  color: #3F4E65;
  border-right: 1px dashed rgba(63, 78, 101, .3);

  &:last-child {
    border: none;
    opacity: 0.5;
  }
}

.sectorTitle {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-bottom: 25px;
}

.title {
  font-weight: 500;
  font-size: 40px;
  line-height: 120%;
  text-transform: uppercase;
  color: #111111;

  @include laptop {
    font-size: 26px;
  }

  @include tablet {
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
  }
}

.contentBlock {
  display: flex;
  flex-direction: row;
  width: 100%;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 100px;

  @include tablet {
    gap: 10px;
  }
}

.sort {
  flex-basis: 20%;
  display: flex;
  flex-direction: column;

  @include mobile {
    display: none;
  }
}

.goods {
  display: flex;
  flex-direction: column;
  align-items: center;
}


